}

.loader {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 1;
    width: 150px;
    height: 150px;
    margin: -75px 0 0 -75px;
    border: 21px dotted#f3f3f3;
    border-radius: 50%;
    border-top: 16px solid #3498db;
    width: 87px;
    height: 90px;
    -webkit-animation: spin 2s linear infinite;
    animation: spin 1s linear infinite;
    /* animation: spin 2s linear infinite; */
    display:block;
  }

  .loaderHide {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 1;
    width: 150px;
    height: 150px;
    margin: -75px 0 0 -75px;
    border: 21px dotted#f3f3f3;
    border-radius: 50%;
    border-top: 16px solid #3498db;
    width: 87px;
    height: 90px;
    -webkit-animation: spin 2s linear infinite;
    animation: spin 1s linear infinite;
    /* animation: spin 2s linear infinite; */
    display:none;
  }

  /* .loader.show{
      display:  block
  } */
  
  @-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  @keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }



